html {
  font-size: 62.5%;
  box-sizing: border-box;
  background-color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: end;
}

.dark-mode {
  background-color: rgb(25.5, 25.5, 25.5);
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode blockquote {
  color: #e1e1e1;
}
.dark-mode .anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio {
  color: #e9f7ef;
}
.dark-mode .anuncio .icono {
  filter: invert(100%);
}
.dark-mode .formulario label, .dark-mode .formulario legend, .dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  color: #FFFFFF;
  border: 1px solid #333333;
}

/** Media Queries **/
.error {
  width: 100%;
  margin: 0 auto;
  font-size: 1.8rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #a93226;
  background-color: rgb(241.5072463768, 199.2536231884, 194.9927536232);
  border-radius: 0.5rem;
  text-align: center;
  margin-bottom: 1rem;
  border-left: 0.5rem solid #a93226;
}

.exito {
  width: 100%;
  margin: 0 auto;
  font-size: 1.8rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #27ae60;
  background-color: rgb(199.8591549296, 242.6408450704, 217.9225352113);
  border-radius: 0.5rem;
  text-align: center;
  margin-bottom: 1rem;
  border-left: 0.5rem solid #27ae60;
}

.header.inicio {
  background-color: #FFFFFF;
}

.registro {
  padding: 2rem;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  background-color: #FFFFFF;
}

@media (min-width: 768px) {
  .registro {
    width: 70rem;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    margin: 0 auto;
  }
  .registro .derecha {
    width: 100%;
    height: 100%;
    background-image: url(/src/img/dereha.webp);
    background-size: cover;
    background-position: center center;
    border-radius: 0.5rem;
  }
}
.seccion {
  display: flex;
  align-items: center;
  height: auto;
}

.formulario {
  display: flex;
  flex-direction: column;
}
.formulario h1 {
  text-align: left;
  font-weight: 700;
  line-height: 5rem;
  margin: 0;
}
.formulario h1::after {
  content: "";
  display: block;
  width: 80%;
  height: 0.5rem;
  background-color: #0C70F2;
  border-radius: 1rem;
  margin-top: 1.6rem;
  margin-bottom: 1.6rem;
}
.formulario label {
  font-size: 2rem;
}
.formulario input {
  padding: 1rem;
  background-color: #ebf5fb;
  border: 1px solid rgb(192.5, 223.75, 242.5);
  border-radius: 0.5rem;
}
.formulario input[type=submit] {
  font-size: 1.8rem;
  font-weight: 700;
  color: #FFFFFF;
  background-color: #0C70F2;
}
.formulario input[type=submit]:hover {
  background-color: rgb(9.5905511811, 89.5118110236, 193.4094488189);
  cursor: pointer;
}
.formulario p {
  font-size: 1.6rem;
  text-align: left;
  margin: 0;
  color: #333333;
}
.formulario p:last-of-type {
  text-align: end;
  margin-top: 1.6rem;
  margin-bottom: 1.6rem;
}
.formulario a {
  color: #0C70F2;
}

.seccion-confirmar {
  width: 100%;
  margin: 0 auto;
}
.seccion-confirmar .btn-confirmar {
  background-color: #0C70F2;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  display: block;
  border-radius: 0.5rem;
  border: none;
  transition: all 0.3s ease-in-out;
}
.seccion-confirmar .btn-confirmar:hover {
  background-color: rgb(10.7952755906, 100.7559055118, 217.7047244094);
  cursor: pointer;
}

.barra {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}
.barra .logo {
  display: flex;
  align-items: center;
  justify-content: center;
}
.barra .logo img {
  width: 8rem;
  height: 8rem;
}
.barra .logo h1 {
  font-weight: 700;
  color: #000000;
}

.navegacion {
  visibility: hidden;
  height: 0;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s ease-in-out, height 0.5s linear;
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 3rem;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    flex-direction: row;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  font-size: 1.7rem;
  font-weight: 700;
  color: #000000;
  align-items: center;
  transition: all 0.2s linear;
}
.navegacion a:last-of-type {
  background-color: #0C70F2;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  display: block;
  border-radius: 0.5rem;
  border: none;
  transition: all 0.3s ease-in-out;
  padding: 0.5rem 1rem;
  margin-right: 3rem;
}
.navegacion a:last-of-type:hover {
  background-color: rgb(10.7952755906, 100.7559055118, 217.7047244094);
  cursor: pointer;
}
.navegacion a:last-of-type:hover {
  background-color: rgb(9.5905511811, 89.5118110236, 193.4094488189);
  color: #FFFFFF;
}
.navegacion a:hover {
  color: #0C70F2;
  transition: all 0.2s linear;
}

.mobile-menu {
  margin: 0 auto;
  cursor: pointer;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.header {
  height: 90vh;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

footer {
  background-color: #ebf5fb;
}
@media (min-width: 768px) {
  footer .contenedor h2 {
    display: none;
  }
}
footer .contenedor {
  padding: 2rem;
}
footer .contenedor h2 {
  margin: 0;
}
footer .contenedor .redes-sociales {
  display: flex;
  column-gap: 1rem;
  padding: 2rem;
  justify-content: center;
}
footer .contenedor .redes-sociales img {
  width: 4rem;
  height: 4rem;
}
footer .contenedor .navegacion {
  justify-content: end;
}
@media (min-width: 768px) {
  footer .contenedor .redes-sociales {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  footer .contenedor .redes-sociales a {
    color: #000000;
  }
}

@media (min-width: 768px) {
  .contenedor-footer {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-content: space-between;
    align-items: center;
    padding: 2rem;
  }
}
.mensaje h1 {
  text-align: center;
}
.mensaje h1::after {
  content: "";
  margin-top: 2rem;
  display: block;
  width: 100%;
}

.contenedor-sidebar {
  width: 100%;
  max-width: 10rem;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 300px) {
  .contenedor-sidebar {
    width: 100%;
    max-width: 8rem;
  }
}
@media (min-width: 768px) {
  .contenedor-sidebar {
    width: 100%;
    max-width: 10rem;
  }
}
@media (min-width: 1028px) {
  .contenedor-sidebar {
    width: 100%;
    max-width: 25rem;
  }
}

.sidebar {
  position: sticky;
  padding: 0.5rem;
  height: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
}
.sidebar h1 {
  font-size: 3rem;
  font-weight: 700;
  margin: 0;
}
@media (min-width: 768px) {
  .sidebar .elementos {
    display: flex;
    flex-direction: column;
  }
  .sidebar .elementos a span {
    display: block;
  }
}
.sidebar .activo {
  fill: #0C70F2;
  background-color: rgb(219, 234, 254);
  color: #0C70F2;
  border-radius: 1rem;
}
.sidebar .activo svg {
  color: #0C70F2;
}
.sidebar a {
  background-color: #FFFFFF;
  display: flex;
  align-items: center;
  color: #000000;
  margin: 0;
  font-size: 1.5rem;
  font-weight: 700;
  margin: 1rem 1rem 0em 1rem;
  padding: 1rem;
  transition: all 0.2s ease-in;
  border-radius: 0.5rem;
}
.sidebar a img,
.sidebar a svg {
  color: #000000;
  margin-right: 1rem;
  width: 2.5rem;
  height: 2.5rem;
}
.sidebar a:not(.activo):hover {
  background-color: #e1e1e1;
  border-radius: 0.5rem;
  transition: all 0.1s ease-in;
}
.sidebar a span {
  display: block;
}
@media (max-width: 1028px) {
  .sidebar h1 {
    display: none;
  }
  .sidebar .elementos {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
  }
  .sidebar .elementos a {
    padding: 1rem;
  }
  .sidebar .elementos a svg,
  .sidebar .elementos a img {
    margin: 0;
  }
  .sidebar .elementos a span {
    display: none;
  }
}
.sidebar .logo h1 {
  text-align: left;
  margin-top: 1rem;
  margin-bottom: 2rem;
  font-size: 2.5rem;
  margin-left: 1.8rem;
}

.dashboard {
  display: flex;
  height: 100vh;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
}

.barra-dashboard {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #FFFFFF;
  border-radius: 1rem;
  border: 1px solid #e1e1e1;
  padding: 1rem 2rem;
  margin: 1rem;
}
.barra-dashboard h3 {
  font-size: 2.5rem;
  margin: 0;
}
.barra-dashboard span {
  color: #0C70F2;
}
.barra-dashboard a {
  background-color: #0C70F2;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  display: flex;
  border-radius: 0.5rem;
  border: none;
  transition: all 0.3s ease-in-out;
  padding: 0.5rem 1rem;
  margin: 0;
}
.barra-dashboard a:hover {
  background-color: rgb(10.7952755906, 100.7559055118, 217.7047244094);
  cursor: pointer;
}

.modal {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.formulario-modal {
  width: 60rem;
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
  margin: 0 auto;
  transform: translateY(-50rem);
  transition: all 0.5s ease-in-out;
  opacity: 0;
}
@media (min-width: 300px) {
  .formulario-modal {
    width: 35rem;
  }
}
@media (min-width: 768px) {
  .formulario-modal {
    width: 60rem;
  }
}
.formulario-modal h2 {
  font-weight: 700;
  margin: 0;
}
.formulario-modal.animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.formulario-modal.cerrar {
  transform: translateY(-200%);
  opacity: 0;
}
.formulario-modal .componentes {
  display: flex;
  flex-direction: column;
}
.formulario-modal .componentes button {
  background-color: #FFFFFF;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  display: flex;
  border-radius: 0.5rem;
  border: none;
  transition: all 0.3s ease-in-out;
  align-items: center;
  column-gap: 1rem;
  text-align: initial;
  color: #000000;
  font-size: 1.8rem;
  border: 1px solid #e1e1e1;
  margin: 0.5rem;
}
.formulario-modal .componentes button:hover {
  background-color: rgb(242.25, 242.25, 242.25);
  cursor: pointer;
}
.formulario-modal .componentes button img {
  width: 1.8rem;
  height: 1.8rem;
}
.formulario-modal .componentes button:hover {
  filter: invert(100%);
}
.formulario-modal .cerrar-modal {
  background-color: #a93226;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  display: block;
  border-radius: 0.5rem;
  border: none;
  transition: all 0.3s ease-in-out;
  width: 100%;
  margin-top: 2rem;
}
.formulario-modal .cerrar-modal:hover {
  background-color: rgb(148.1811594203, 43.8405797101, 33.3188405797);
  cursor: pointer;
}

.componente {
  width: 70rem;
}
@media (min-width: 300px) {
  .componente {
    width: 35rem;
    max-height: 70rem;
    overflow-y: scroll;
  }
}
@media (min-width: 768px) {
  .componente {
    width: 70rem;
  }
}
.componente .regresar-btn {
  background-color: #0C70F2;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  display: block;
  border-radius: 0.5rem;
  border: none;
  transition: all 0.3s ease-in-out;
}
.componente .regresar-btn:hover {
  background-color: rgb(10.7952755906, 100.7559055118, 217.7047244094);
  cursor: pointer;
}
.componente .caracteristicas {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1rem;
}
@media (min-width: 300px) {
  .componente .caracteristicas {
    display: flex;
    flex-direction: column;
    height: auto;
  }
}
@media (min-width: 768px) {
  .componente .caracteristicas {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1rem;
  }
}
.componente .caracteristicas div {
  display: flex;
