/* Define la animación de pulsación */
@keyframes pulsar {
  0% {
    opacity: 1;
    stroke: #0C70F2;
  }
  50% {
    opacity: 0.5;
    stroke: #0C70F2;
  }
  100% {
    opacity: 1;
    stroke: #0C70F2;
